import React from 'react'
import OurworkData from './OurworkData';



const Ourwork = () => {
  const WorkCard = (props) =>{
       return(
           <>
            <div className="col-md-4">
                <div className="workbox">
                    <div className="thumbnail">
                        <img className='img-fluid' src={props.imgser} alt=""  />
                    </div>
                    <p>{props.content}</p>
                </div>
            </div>
           </>
       );
   }

  return (
       <article className="ourwork py-5">
           <div className="container">
               <div className="row">
                   <div className="col-md-12">
                       <h3 className='m-0'>OUR WORK PROCESS</h3>
                       <span></span>
                   </div>
               </div>
               <div className="row py-5">
                  {OurworkData.map((val)=>{
                     return(
                        <WorkCard key={val.id} imgser={val.imgser} content={val.content}/>
                     );
                  })};
                   
               </div>
           </div>
       </article>
  )
}

export default Ourwork;
