import React from 'react'

const Card = (props) => {
  
  return (
    <>
        <div className='cards'>
            <div className='card'>
                <span>{props.sn}</span>
                <h4>{props.title}</h4>
                <p>{props.discrip}</p>
            </div>
        </div>
    </>
  )
}

export default Card;
