import React from 'react'
import Custreviewslider from './Custreviewslider';
import SliderData from './SliderData';

const Customerr = () => {
   
  return (
     <>
         <section className="customer_review py-5">
             <div className="container abs">
                 <div className="row">
                     <div className="col-md-12 text-center py-5">
                         <h5>Customer review</h5>
                         <span className='border_bot'></span>
                     </div>
                 </div>
                 <div className="row">
                 <div className="col-md-12">
                       <div id="carouselExampleFade" className="carousel slide carousel-fade" data-bs-ride="carousel">
                            <div className="carousel-inner">
                            
                            
                               {SliderData.map((custmval, i)=>{
                                return(
                                
                                <div key={ i } className={ i === 0 ? "carousel-item active  d-flex align-items-center justify-content-center" : "carousel-item d-flex align-items-center justify-content-center" }>

                                 <Custreviewslider imgser={custmval.imgser} sname={custmval.sname} disc={custmval.disc} rdlink={custmval.rdlink}/>
                                  </div>
                                );
                               })}
                               
                               
                            </div>
                                <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="prev">
                                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span className="visually-hidden">Previous</span>
                                </button>
                                <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="next">
                                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                <span className="visually-hidden">Next</span>
                        </button>
                    </div>

                  
                  </div>
                 </div>
             </div>
         </section>
     </>
  )
}

export default Customerr;
