import React from 'react'
import Header from './Header';
import Footer from './Footer';
import contImg from "../images/contact.png";
const Contactus = () => {
  return (
    <> 
       <Header/>
        <section className="superHero py-5">
            <div className="container py-5">
                <div className="row">
                    <div className="col-md-6 order-2">
                       <img src={contImg} className="img-fluid" alt="" />
                    </div>
                    <div className="col-md-6 order-1">
                    <h1>Contactus section</h1>
                        <form>
                            <div className="mb-3">
                            <label htmlFor="exampleInputEmail1" className="form-label">Full Name</label>
                            <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
                            </div>
                            <div className="mb-3">
                            <label htmlFor="exampleInputEmail1" className="form-label">Mobile Number</label>
                            <input type="number" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
                            </div>
                            <div className="mb-3">
                            <label htmlFor="exampleInputEmail1" className="form-label">Email Id</label>
                            <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
                            </div>
                            <div className="mb-3">
                            <label htmlFor="exampleInputEmail1" className="form-label">Message</label>
                            <input type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
                            </div>
                            
                        <button type="submit" className="btn btn-primary">Submit</button>
                        </form>
                    </div>
                </div>
            </div>
        </section>
        <Footer/>
    </>
  )
}

export default Contactus;

