import React from 'react'
import cleaningSer from "../images/cleaning_service.png"

const CleaningSer = () => {
  return (
   <>
       <section className="cleaningser py-5">
           <div className="container p5-3">
               <div className="row">
                   <div className="col-md-6 m-auto">
                        <img src={cleaningSer} className="img-fluid" alt="cleaning services" />
                   </div>
               </div>
           </div>
       </section>
   </>
  )
}

export default CleaningSer;
