import React from 'react'
import { FaFacebookF } from "react-icons/fa";
import { FaTwitter } from "react-icons/fa";
import { FaInstagram } from "react-icons/fa";
import { FaLinkedinIn } from "react-icons/fa";
const Footer = () => {
  return (
    <>
        <footer className="footer py-4">
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        <h5>Follow Us</h5>
                        <div className='headIcon text-white'>
                            <ul className='d-flex align-items-conter m-0 justify-content-center'>
                                <li className='ms-1'><FaFacebookF className='socI'/></li>
                                <li className='ms-1'><FaTwitter className='socI'/></li>
                                <li className='ms-1'><FaInstagram className='socI'/></li>
                                <li className='ms-1'><FaLinkedinIn className='socI'/></li>
                            </ul>
                       </div>
                    </div>
                </div>
            </div>
        </footer>
        <div className='copywrite py-3'>
            <p>Copyright 2022 All Right Reserved By Vinod Patil</p>
        </div>
    </>
  )
}

export default Footer;
