import React from 'react'

const Requestcallback = () => {
    let googleMap = "https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d121058.92836615497!2d73.79292730116279!3d18.524766325796897!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc2bf2e67461101%3A0x828d43bf9d9ee343!2sPune%2C%20Maharashtra!5e0!3m2!1sen!2sin!4v1654926443434!5m2!1sen!2sin";

    const styleCassmap = {
        width : "100%", 
        height: "100%", 
        border :"0", 
        allowfullscreen :"", 
        loading :"lazy", 
        referrerpolicy :"no-referrer-when-downgrade",
    }


  return (
    <>
        <article className="requestcall">
              <div className="col-md-6 bgFirst p-5">
                         <div className="formCont">
                         <h2>request a call back</h2>
                         <span></span>
                            <form>
                                <div className="mb-3">
                                   <input type="text" className="form-control" placeholder='Name'/>
                                 </div>
                                 <div className="mb-3">
                                   <input type="number" className="form-control" placeholder='Phone Number'/>
                                 </div>
                                 <div className="mb-3">
                                   <input type="text" className="form-control" placeholder='Email'/>
                                 </div>
                                 <div className="mb-3">
                                 <input type="text" className="form-control" placeholder='Message'/>
                                 </div>
                                <button type="submit" className="btn submitBtn">Submit</button>
                            </form>
                         </div>
                    </div>
                    <div className="col-md-6">
                       <iframe src={googleMap} style={styleCassmap}/>
                    </div>
         </article>
    </>
  )
}

export default Requestcallback;
