import React from 'react'

const Custreviewslider = (props) => {



 return (
   <>
     
    
            <div className='papper'>
               <span>{props.id}</span>
               <img src={props.imgser} className="thumb" alt="..."/>
               <h6>{props.sname}</h6>
                  <p>{props.disc}</p>
               <a href={props.rdlink} target="_blank">
                     <button className='btn btn-info'>Read More</button>
               </a>
            </div>
     

      
   </>
  )
}

export default Custreviewslider;
